import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { starLevel } from "../Skills/skills";

const Welcome = props => {
  const { skills, contact } = props;

  const skillSummary = skills.map(skill => {
    const level = starLevel(skill.level);
    return (
      <p key={Math.random()}>
        <b>{skill.name}: </b>
        {level}
      </p>
    );
  });

  return (
    <div>
      <div className="row bg-light p-5 mb-5 text-left welcome">
        <div className="col-md-7 col-sm-12">
          <h1 className="display-4">Thanh Nguyen's Portfolio</h1>
          <hr className="my-4" />
          <p>
            Welcome to my portfolio! My name is Thanh Nguyen - a fresh Frontend
            Developer.
          </p>
          <p>I hope that you enjoy your visit!</p>
          <Link className="btn btn-lg btn-primary" to="/skills">
            Hire Me!
          </Link>
        </div>
        <div className="col-md-5 col-sm-0" />
      </div>
      <div className="row">
        <div className="col-6">
          <div className="card">
            <h5 className="card-header">Skills</h5>
            <div className="card-body">
              <div className="card-text">{skillSummary}</div>
              <Link to="/skills" className="btn btn-primary">
                Details
              </Link>
            </div>
          </div>
        </div>
        <div className="col-6">
          <div className="card">
            <h5 className="card-header">Contact Me</h5>
            <div className="card-body">
              <p className="card-text">
                <b>Phone:</b> {contact.phone}
              </p>
              <p className="card-text">
                <b>Email :</b> {contact.email}
              </p>
              <Link to="/contact" className="btn btn-primary">
                Details
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return { skills: state.skills, contact: state.contact };
};

export default connect(mapStateToProps)(Welcome);
