import React from "react";
import { connect } from "react-redux";

const Contact = props => {
  const { contact } = props;

  const {
    name,
    dob,
    email,
    address,
    city,
    country,
    phone,
    nationality
  } = contact;
  return (
    <div className="container border border-dark my-5 p-0">
      <h2 className="display-5 p-2 bg-info text-white">
        <i className="material-icons">contacts</i> Contact Information
      </h2>
      <div className="container">
        <div className="p-3 row">
          <div className="col-md-6 col-sm-12">
            <p>
              <b>Full name:</b> {name}
            </p>
            <p>
              <b>Date of Birth:</b> {dob}
            </p>
            <p>
              <b>Email:</b> {email}
            </p>
            <p>
              <b>Phone:</b> {phone}
            </p>
          </div>
          <div className="col-md-6 col-sm-12">
            <p>
              <b>Nationality:</b> {nationality}
            </p>
            <p>
              <b>Address:</b> {address}
            </p>
            <p>
              <b>City:</b> {city}
            </p>
            <p>
              <b>Country:</b> {country}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    contact: state.contact
  };
};

export default connect(mapStateToProps)(Contact);
