import React from "react";
import programmer from "./programmer.jpg";
import { HashLink as Link } from "react-router-hash-link";

const AboutMe = () => {
  return (
    <div className="container border border-dark my-5 p-0">
      <h2 className="display-5 p-2 bg-info text-white">
        <i className="fa fa-info-circle" /> About Me
      </h2>
      <div className="p-3">
        <blockquote className="blockquote">
          <p className="mb-0">
            "Always code as if the guy who ends up maintaining your code will be
            a violent psychopath who knows where you live"
          </p>
          <footer className="blockquote-footer">
            John Woods from <cite title="Source Title">Goodreads</cite>
          </footer>
        </blockquote>
        <div className="clearfix">
          <h3 className="text-center mb-4">
            This is a story of how I started my Developer life, with history and
            stuff. Thanks for passing by!
          </h3>
          <h4 className="mb-4">
            Brief personal information: My name is Thanh Nguyen and I am a
            24-year-old Vietnamese guy. I graduated as an Automation Engineer.
          </h4>
          <Link className="" to="/about#tldr">
            TL;DR
          </Link>

          <p>
            Being a developer (for the moment, Frontend Developer) suits me
            well. Whenever I manage to think up a new cool project or solve a
            problem that have kept me thinking about for days, I feel a great
            sense of accomplishment. Seeing how the code performs exactly like
            how I imagine it in my mind is like seeing a godlike being. That was
            exaggerated, but it is somewhat like that.
          </p>
          <p>
            I have always liked logical thinking. All the math exercises,
            quizzes, mystery fictions. However, I didn't really like programming
            when I was a middlde schooler. I actually hated it, because we were
            taught Pascal, and how ancient that programming language is. So I am
            not the one who has a dream to begin with.
          </p>
          <p>
            Then came the next stop of my life: college. As a clueless teenager,
            I asked for my parents' wise guidance, and they advised me to go for
            Automation Engineer since the job was (probably still is) the hot
            topic back then. I gladly followed it and I got accepted to a
            Finnish University Of Applied Sciences named HAMK.
          </p>
          <img src={programmer} alt="programmer" className="float-right mx-2" />
          <p>
            College life went on smoothly, I found the major pretty fun and I
            thought that I would be happy enough as an automation engineer.
            However, in my last year, I slowly came to the realization that I
            just liked the programming part, and in order to be eligible to
            program in this major, the experience requirement is usually around
            7-10 years. And if I wanted to change the field, I would have to
            finish this one, then take another 4 years. I tried learning C and
            C++ on my own, the progress was there but not fast enough, and I had
            a hard time understanding many concepts and rules of those
            languages.
          </p>

          <p>
            As a clueless college student, I asked for advices again, and the
            recommendation was to study the trinity HTML, CSS and Javascript.
            Best recommendation ever, Duc. I quickly fell in love with the idea
            of being a Frontend developer, and the languages also. I have been
            learning what I need for nearly a year now, and I am still improving
            day by day. Now the next step is that I need is a real experience, a
            job. That will significantly improve my skills and confidence. If
            you are a recruiter, please go and check my other tabs. Thank you!
          </p>

          <hr className="my-3" />
          <p id="tldr">
            <b>TL;DR:</b> I studied Automation Engineering and graduated, but I
            liked programming. Therefore, I have been studying anything related
            to being a Frontend Developer on my own for nearly a year now. And I
            am looking for jobs!
          </p>
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
