import React from "react";
import { connect } from "react-redux";

const Skills = props => {
  const { skills } = props;
  const skillsDisplayed = skills.map(skill => {
    const level = starLevel(skill.level);
    return (
      <div className="card border-dark my-4" key={skill.id}>
        <div className="card-body p-3">
          <h5 className="card-title">{skill.name} </h5>
          <h6 className="card-subtitle">{level}</h6>
          <div className="card-body p-3">{skill.desc}</div>
        </div>
      </div>
    );
  });

  return (
    <div className="container my-5 p-0">
      <h2 className="display-5 p-2 bg-info text-white">
        <i className="fas fa-th" /> Skills
      </h2>
      <React.Fragment>{skillsDisplayed}</React.Fragment>
    </div>
  );
};

export const starLevel = level => {
  let stars = [];
  for (let i = 0; i < 5; i++) {
    if (i < level) {
      if (i === level - 0.5)
        stars.push(<i className="fa fa-star-half-o" key={Math.random()} />);
      else stars.push(<i className="fa fa-star" key={Math.random()} />);
    } else {
      stars.push(<i className="fa fa-star-o" key={Math.random()} />);
    }
  }
  return stars;
};

const mapStateToProps = state => {
  return { skills: state.skills };
};
export default connect(mapStateToProps)(Skills);
