import React from "react";

const Experiences = () => {
  return (
    <div className="container my-5 p-0">
      <h2 className="display-5 p-2 bg-info text-white">
        <i className="fas fa-briefcase" /> Experiences
      </h2>

      <div className="card my-4 border-dark">
        <div className="card-body p-3">
          <h5 className="card-title">
            HAMK University of Applied Sciences - Design an automatic conveyor
            project
          </h5>
          <h6 className="card-subtitle">02.2014 – 05.2014</h6>
          <p className="card-body p-3">
            In this project, my job was to code the PLC S7-300 using the program
            Simantic Step 7. The hardware was setup before-hand. The purpose of
            this project was to create a system for transporting products using
            two conveyors and two lifts. Later, I designed the user interface
            for this project, which consisted of Start/Stop buttons, indicators
            showing where the product currently is, which positions the lifts
            are in, using Microsoft Visual Studio.
          </p>
        </div>
      </div>

      <div className="card my-4 border-dark">
        <div className="card-body p-3">
          <h5 className="card-title">
            SolarBK Company - Automation Engineer Intern
          </h5>
          <h6 className="card-subtitle">03.2016 – 07.2016</h6>
          <p className="card-body p-3">
            I worked as part of the automation team in SolarBk. My job was to
            code the PLC so that it will transfer data to a backup server
            through File Transfer Protocol. I worked with TIA portal and PLC
            S7-1200, and the programming language I used was Ladder Logic.
          </p>
        </div>
      </div>

      <div className="card my-4 border-dark">
        <div className="card-body p-3">
          <h5 className="card-title">
            HAMK University of Applied Sciences - Residential Security System
            thesis
          </h5>
          <h6 className="card-subtitle">11.2018 – 01.2019 </h6>
          <p className="card-body p-3">
            This project was my thesis, and the idea was all mine. In this
            project, I designed a low-cost security system using the Arduino.
            The system was designed with password lock, multiple security modes
            and wireless communication using assigned phone calls and SMS. The
            password, modes, and assigned phone numbers can be changed easily if
            one can bypass the lock phase, which is inputting the correct
            password. For detailed information, please visit:{" "}
            <a
              className="card-link"
              href="https://www.theseus.fi/handle/10024/158975 "
            >
              Thanh Nguyen's thesis
            </a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Experiences;
